import React from "react";

function Menu() {
  return (
    <div className="menu">
      <div className="row">
        <div className="col-5">
          <img
            className="w-100"
            src={process.env.PUBLIC_URL + "/images/profile.jpg"}
            alt="profile"
          />
        </div>
        <div className="col-7 my-auto">
            
        <h1>Hello! I'm Kiky.</h1>
        <br />
        <p className="content">2nd year Computing student at Imperial College London</p>
        </div>
      </div>
    </div>
  );
}

export default Menu;
